@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card:hover .card:hover__product-name {
  color: #34568b;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-name, .card__price-value {
  color: #060b35;
  margin: 0;
  font-family: Roboto, serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__price-value, .card__buy {
  font-family: Roboto, serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
}

.card__product-code, .card__reviews, .card__product-price {
  color: #616070;
  margin: 0;
  font-family: Roboto, serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.card__product-code {
  width: 100%;
  margin-top: 4px;
}

.card__reviews {
  color: #060b35;
}

.card__product-price {
  font-size: 12px;
  line-height: 18px;
}

.card__rating, .card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.card__buy {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  line-height: 16px;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  transition: all .3s;
}

.card__rating, .card__buy {
  margin-top: 16px;
}

.cards {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  column-gap: 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.cards__card {
  margin-bottom: 46px;
  margin-right: 48px;
}

.stars {
  flex-direction: row;
  width: fit-content;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  cursor: pointer;
  text-decoration: none;
  display: flex;
}

.logo__img {
  object-fit: contain;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  cursor: pointer;
  color: #060b35;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav .is-active, .nav__link:hover {
  color: #00acdc;
  transition: all .3s;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
